.notecomp {
  cursor: pointer;
  display: flex;
  align-items: center;
  background-color: #f4f6f8;
  border-radius: 1.25rem;
  margin-top: 1rem;
  transition: all 0.5s ease;
}
.notecomp .icon {
  border-radius: 1.25rem;
  width: 3.75rem;
  height: 3.75rem;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #e6ecfc;
  transition: all 0.5s ease;
}
.notecomp .icon img {
  width: 2.5rem;
}
.notecomp h1 {
  white-space: nowrap;
  overflow: hidden !important;
  text-overflow: clip;
  max-width: "100%";
  margin-left: 1rem;
  font-size: 1.25rem;
}

.notecomp:hover {
  background-color: #e7edf3;
}
.notecomp:hover .icon {
  background-color: #d1d9f1;
}